/**
 * Conversation Preset Persistence Utility
 * Handles saving, loading, and versioning of conversation presets
 */

import {
  ConversationPreset,
  ConversationPresetHistory,
  ABTestResults,
} from "./types";

/**
 * Storage keys for localStorage
 */
const STORAGE_KEYS = {
  PRESETS: "conversation_presets",
  HISTORY: "conversation_preset_history",
  AB_TESTS: "conversation_ab_tests",
  DEFAULT_PRESET: "conversation_default_preset",
};

/**
 * Preset persistence class
 */
export class ConversationPresetPersistence {
  /**
   * Save a preset to localStorage
   */
  static savePreset(preset: ConversationPreset): void {
    try {
      const existingPresets = this.getAllPresets();
      const updatedPresets = existingPresets.filter((p) => p.id !== preset.id);
      updatedPresets.push(preset);

      localStorage.setItem(
        STORAGE_KEYS.PRESETS,
        JSON.stringify(updatedPresets),
      );

      // Update history
      this.addToHistory(preset);
    } catch (error) {
      console.error("Failed to save preset:", error);
      throw new Error("Failed to save preset to storage");
    }
  }

  /**
   * Load a preset by ID
   */
  static loadPreset(id: string): ConversationPreset | null {
    try {
      const presets = this.getAllPresets();
      return presets.find((p) => p.id === id) || null;
    } catch (error) {
      console.error("Failed to load preset:", error);
      return null;
    }
  }

  /**
   * Get all saved presets
   */
  static getAllPresets(): ConversationPreset[] {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.PRESETS);
      return stored ? JSON.parse(stored) : [];
    } catch (error) {
      console.error("Failed to load presets:", error);
      return [];
    }
  }

  /**
   * Delete a preset
   */
  static deletePreset(id: string): void {
    try {
      const existingPresets = this.getAllPresets();
      const filteredPresets = existingPresets.filter((p) => p.id !== id);
      localStorage.setItem(
        STORAGE_KEYS.PRESETS,
        JSON.stringify(filteredPresets),
      );
    } catch (error) {
      console.error("Failed to delete preset:", error);
      throw new Error("Failed to delete preset from storage");
    }
  }

  /**
   * Set default preset
   */
  static setDefaultPreset(preset: ConversationPreset): void {
    try {
      localStorage.setItem(STORAGE_KEYS.DEFAULT_PRESET, JSON.stringify(preset));
    } catch (error) {
      console.error("Failed to save default preset:", error);
    }
  }

  /**
   * Get default preset
   */
  static getDefaultPreset(): ConversationPreset | null {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.DEFAULT_PRESET);
      return stored ? JSON.parse(stored) : null;
    } catch (error) {
      console.error("Failed to load default preset:", error);
      return null;
    }
  }

  /**
   * Add preset change to history
   */
  static addToHistory(preset: ConversationPreset): void {
    try {
      const history = this.getHistory();

      // Create history entry
      const historyEntry: ConversationPresetHistory = {
        id: `history_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`,
        presetId: preset.id,
        version: (history.length + 1).toString(),
        appliedAt: new Date().toISOString(),
        appliedBy: "user", // Could be extended to include actual user
        changes: {
          presetId: preset.id,
          summary: {
            totalChanges: 1, // Simplified for now
            affectedCategories: ["responseDynamics", "timingControls"],
            riskDistribution: { low: 1, medium: 0, high: 0, critical: 0 },
          },
          changes: [
            {
              path: "preset.updated",
              changeType: "modified" as const,
              oldValue: "previous",
              newValue: "current",
              riskLevel: "low",
            },
          ],
        },
        performanceMetrics: {
          responseTime: 850 + Math.random() * 300, // Simulated metrics
          qualityScore: 0.7 + Math.random() * 0.3,
          userSatisfaction: 0.6 + Math.random() * 0.4,
          errorRate: Math.random() * 0.1,
        },
        rollbackAvailable: true,
      };

      // Keep only last 50 history entries
      const updatedHistory = [...history, historyEntry].slice(-50);
      localStorage.setItem(
        STORAGE_KEYS.HISTORY,
        JSON.stringify(updatedHistory),
      );
    } catch (error) {
      console.error("Failed to add to history:", error);
    }
  }

  /**
   * Get preset history
   */
  static getHistory(): ConversationPresetHistory[] {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.HISTORY);
      return stored ? JSON.parse(stored) : [];
    } catch (error) {
      console.error("Failed to load history:", error);
      return [];
    }
  }

  /**
   * Clear history
   */
  static clearHistory(): void {
    try {
      localStorage.removeItem(STORAGE_KEYS.HISTORY);
    } catch (error) {
      console.error("Failed to clear history:", error);
    }
  }

  /**
   * Rollback to a previous preset
   */
  static rollback(historyEntry: ConversationPresetHistory): ConversationPreset {
    try {
      // Get the preset by ID
      const preset = this.loadPreset(historyEntry.presetId);
      if (!preset) {
        throw new Error(`Preset ${historyEntry.presetId} not found`);
      }
      return preset;
    } catch (error) {
      console.error("Failed to rollback:", error);
      throw new Error("Failed to rollback to previous preset");
    }
  }

  /**
   * Start A/B test
   */
  static startABTest(
    presetA: ConversationPreset,
    presetB: ConversationPreset,
  ): string {
    try {
      const testId = `ab_test_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;

      const abTest = {
        id: testId,
        startedAt: new Date().toISOString(),
        endedAt: null,
        presetA,
        presetB,
        status: "running",
        results: null,
      };

      const existingTests = this.getABTests();
      existingTests.push(abTest);
      localStorage.setItem(
        STORAGE_KEYS.AB_TESTS,
        JSON.stringify(existingTests),
      );

      return testId;
    } catch (error) {
      console.error("Failed to start A/B test:", error);
      throw new Error("Failed to start A/B test");
    }
  }

  /**
   * Get A/B test results
   */
  static getABTestResults(testId: string): ABTestResults | null {
    try {
      const tests = this.getABTests();
      const test = tests.find((t) => t.id === testId);

      if (!test || test.status !== "completed") {
        // Simulate some results for demonstration
        const simulatedResults: ABTestResults = {
          testId,
          startDate: new Date(
            Date.now() - 7 * 24 * 60 * 60 * 1000,
          ).toISOString(),
          endDate: new Date().toISOString(),
          variants: {
            A: {} as ConversationPreset, // Placeholder preset A
            B: {} as ConversationPreset, // Placeholder preset B
          },
          metrics: [
            {
              variant: "A",
              sampleSize: 1000,
              averageResponseTime: 850,
              qualityScore: 0.78,
              userSatisfaction: 0.72,
              errorRate: 0.03,
              engagementLevel: 0.75,
            },
            {
              variant: "B",
              sampleSize: 1000,
              averageResponseTime: 780,
              qualityScore: 0.81,
              userSatisfaction: 0.76,
              errorRate: 0.02,
              engagementLevel: 0.82,
            },
          ],
          statisticalSignificance: 0.96,
          confidenceInterval: {
            lower: 0.02,
            upper: 0.08,
            metric: "qualityScore",
          },
          recommendation: "B",
        };

        return simulatedResults;
      }

      return test.results;
    } catch (error) {
      console.error("Failed to get A/B test results:", error);
      return null;
    }
  }

  /**
   * Get all A/B tests
   */
  static getABTests(): any[] {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.AB_TESTS);
      return stored ? JSON.parse(stored) : [];
    } catch (error) {
      console.error("Failed to load A/B tests:", error);
      return [];
    }
  }

  /**
   * Export presets and history
   */
  static exportData(): string {
    try {
      const data = {
        presets: this.getAllPresets(),
        history: this.getHistory(),
        abTests: this.getABTests(),
        exportedAt: new Date().toISOString(),
        version: "1.0",
      };

      return JSON.stringify(data, null, 2);
    } catch (error) {
      console.error("Failed to export data:", error);
      throw new Error("Failed to export data");
    }
  }

  /**
   * Import presets and history
   */
  static importData(jsonData: string): void {
    try {
      const data = JSON.parse(jsonData);

      if (data.presets) {
        localStorage.setItem(
          STORAGE_KEYS.PRESETS,
          JSON.stringify(data.presets),
        );
      }

      if (data.history) {
        localStorage.setItem(
          STORAGE_KEYS.HISTORY,
          JSON.stringify(data.history),
        );
      }

      if (data.abTests) {
        localStorage.setItem(
          STORAGE_KEYS.AB_TESTS,
          JSON.stringify(data.abTests),
        );
      }
    } catch (error) {
      console.error("Failed to import data:", error);
      throw new Error("Failed to import data - invalid format");
    }
  }

  /**
   * Clear all data
   */
  static clearAll(): void {
    try {
      Object.values(STORAGE_KEYS).forEach((key) => {
        localStorage.removeItem(key);
      });
    } catch (error) {
      console.error("Failed to clear data:", error);
    }
  }
}
